* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: rgb(252, 240, 229);
}

#root {
  max-width: 550px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 20px auto;
  background-color: #fff;
  border-radius: 15px;
}

nav {
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
}

nav > img {
  width: 140px;
  padding: 10px 20px;
}

section {
  padding: 20px;
  margin: 20px 0;
}

.main-image {
  max-width: 400px;
  object-fit: contain;
  margin: auto;
  display: block;
  margin-bottom: 30px;
}

.card img {
  border-radius: 15px;
}

.star {
  height: 20px;
  align-self: center;
  padding-right: 5px;
}

.card p {
  margin-top: 5px;
}

.country {
  color: #918e9b;
}

.card {
  position: relative;
}

.card > div {
  display: flex;
  /* margin: 5px 0; */
}

.price {
  font-weight: bold;
}

.card-container {
  display: flex;
  flex-direction: row;
  gap: 20px;
  overflow-x: scroll;
  width: 500px;
}

.card-badge {
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}
